$ns: "fdt-";
@import 'style/global.scss';

.#{$ns}CatalogListItem {
    position: relative;

    display: flex;
    overflow: hidden;
    flex-direction: column;

    min-width: calc(25% - (100vh * 16 / #{$base-height}));
    max-width: calc(25% - (100vh * 16 / #{$base-height}));
    margin-right: scale-w(16);

    border-radius: scale-h(12);
    background: rgba(255, 255, 255, .104);
    background-color: rgba(255, 255, 255, .05);

    transition: .4s ease-in-out;
    will-change: transform;
    will-change: background-color;

    &__card {
        max-height: scale-h(200);
    }

    &__img {
        width: 100%;
        max-width: 100%;
    }

    &:focus {
        background-color: rgba(255, 255, 255, .1);

        transform: scale(1.04);

        .#{$ns}CatalogListItem {
            &__callToAction {
                opacity: 1;
            }
        }

    }

    &__row {
        flex-grow: 1;

        margin-bottom: scale-h(12);
    }

    &__description {
        position: relative;

        display: flex;
        flex-grow: 1;
        flex-direction: column;

        padding: scale-h(16) scale-w(12) scale-h(12) scale-w(12);
    }

    &__priceRow {
        display: flex;
        align-items: center;
    }

    &__title {
        font-size: scale-h(16);
        font-weight: 500;
        font-style: normal;
        letter-spacing: -.019em;

        color: #fff;
    }

    &__name {
        line-height: scale-h(22);
    }

    &__callToAction {
        position: absolute;
        top: scale-h(-26);
        right: scale-w(12);

        display: flex;
        justify-content: center;
        align-items: center;

        width: scale-h(38);
        height: scale-h(38);

        opacity: 0;
        border-radius: 50%;
        background: $sc-accent-green;
        box-shadow: 0 8px 25px rgba(0, 0, 0, .1);

        transition: opacity .2s ease-in-out;

        &::before {
            top: 1px;

            display: block;

            width: scale-h(20);
            height: scale-h(20);
            margin-right: 2px;

            content: '';

            background-image: url('../../../assets/icons/plus.svg');
            background-repeat: no-repeat;
            background-size: contain;
        }
    }

    &__price {
        font-weight: 600;
        line-height: scale-h(28);
    }

    &__badge {
        display: flex;
        justify-content: center;
        align-items: center;

        padding-left: scale-w(4);

        font-weight: 600;

        color: $sc-accent-green;

        &::before {
            position: relative;
            position: relative;
            top: 1px;

            display: block;

            width: scale-w(10);
            height: scale-h(10);
            margin-right: 2px;

            content: '';

            background-image: url('../../../assets/icons/cross.svg');
            background-repeat: no-repeat;
            background-size: contain;
        }
    }
}
