$ns: "fdt-";
@import 'style/global.scss';

.#{$ns}HeaderCart {
    display: flex;
    align-items: center;

    margin-left: 8px;

    border-radius: 40px;
    padding: 20px 40px;

    height: 80px;
    box-sizing: border-box;

    transition: .2s ease-in-out;
    will-change: background-color;

    &:focus {
        background: #08A652;

        .#{$ns}HeaderCart {
            &__badge {
                color: #08A652;

                background: #fff;
                border: 2px solid #08A652;
            }
        }
    }

    &__icon {
        position: relative;

        width: 48px;
        height: 48px;

        background-image: url('./cart.svg');
        background-repeat: no-repeat;
        background-size: contain;
    }

    &__badge {
        position: absolute;
        top: -8px;
        right: -8px;

        display: flex;
        justify-content: center;
        align-items: center;

        width: 32px;
        height: 32px;
        line-height: 28px;

        font-size: 20px;

        box-sizing: border-box;

        color: #fff;
        border-radius: 50%;
        background: #08A652;

        transition: .2s ease-in-out;
        will-change: background-color;
    }

    &__price {
        height: 48px;

        margin-left: 16px;
        font-size: 32px;
        font-weight: 600;
        line-height: 50px;
        color: #fff;
    }
}
