$ns: "fdt-";
.Suggest {
    position: fixed;
    left: 0;
    bottom: 0;
    right: 0;
    display: flex;
    padding: 0 0 28px 128px;
}

.Suggest-Item {
    display: flex;
    box-sizing: border-box;
    align-items: center;
    height: 72px;
    line-height: 72px;
    padding: 16px 32px;
    background: rgba(255, 255, 255, 0.12);
    border-radius: 54px;
    margin-right: 24px;
    color: #fff;
    font-size: 32px;
}
