$ns: "fdt-";
@import 'style/global.scss';

.#{$ns}CatalogList {
    margin-left: 96px;
    margin-top: 96px;

    height: 640px;
    overflow: hidden;

    &__container {
        display: flex;

        padding: 24px 16px 44px;

        height: 660px;

        box-sizing: border-box;

        overflow-x: scroll;

        --spatial-navigation-contain: contain;

        transition: transform .4s ease-in-out;
    }
}
