/**
 * Fonts
 */
@import './SBSansText.scss';
@import './SBSansDisplay.scss';

$base-width: 960;
$base-height: 540;

$background-color: #191a1a;

$sc-accent-green: #08a652;
$sc-green: #34b76f;
$sc-red: #ff486f;

$unit: 4px;

$def-border-radius: 8px;
$def-transition-options: .2s ease-in;

html {
    scroll-behavior: smooth;
}

* {
    //outline: none;
}

a {
    text-decoration: none;

    color: none;
}

a:visited {
    text-decoration: none;
}

a:focus {
    outline: none;
}

@function scale-w($value) {
    $result: calc(100vw * #{$value} / #{$base-width});

    @return $result;
}

@function scale-h($value) {
    $result: calc(100vh * #{$value} / #{$base-height});

    @return $result;
}
