@import 'style/global.scss';

$grid-breakpoints: (
    s: 0,
    m: 600px,
    l: 900px,
    xl: 1200px,
) !default;

// $container-max-widths: (
//   s: 288px,
//   m: 672px,
//   l: 960px,
//   xl: 1152px,
// ) !default;

$container-margin-x-list: (
    s: 20px,
    m: scale-w(56),
    l: scale-w(56),
    xl: scale-w(56),
) !default;

$container-padding-x-list: (
    s: 16px,
    m: 24px,
    l: 24px,
    xl: 32px,
) !default;

$grid-columns: 12;
$grid-gutter-width: 12px;

$grid-columns-list: (
    s: 4,
    m: 6,
    l: 8,
    xl: 12
) !default;

$grid-gutter-width-list: (
    s: 12px,
    m: 16px,
    l: 16px,
    xl: 16px,
) !default;

@mixin make-container($breakpoints: $grid-breakpoints) {

    // $maxXl: 1328 + map-get($container-margin-x-list, 'xl') * 2;
    // max-width: 1328px;

    @each $breakpoint, $container-margin-x in $container-margin-x-list {
        @include media-breakpoint-up($breakpoint, $breakpoints) {
            margin-right: $container-margin-x;
            margin-left: $container-margin-x;
        }
    }

    // @media (min-width: $maxXl) {
    //     margin-right: auto;
    //     margin-left: auto;
    // }
}

@mixin make-row() {
    display: flex;
    flex-wrap: nowrap;
}

@mixin make-col($size, $gutter, $columns) {
    $width: percentage($size / $columns);
    display: flex;
    flex: 0 0 $width;

    max-width: $width;
}

@mixin make-col-offset($size, $gutter, $columns) {
    $num: $size / $columns;
    $perc: percentage($num);
    margin-left: if($num == 0, 0, $perc);
}

@mixin media-breakpoint-up($name, $breakpoints: $grid-breakpoints) {
    $min: breakpoint-min($name, $breakpoints);

    @if $min {
        @media (min-width: $min) {
            @content;
        }
    }

    @else {
        @content;
    }
}

@function breakpoint-infix($name, $breakpoints: $grid-breakpoints) {
    @return if(breakpoint-min($name, $breakpoints) == null, '', '-#{$name}');
}

@function breakpoint-min($name, $breakpoints: $grid-breakpoints) {
    $min: map-get($breakpoints, $name);

    @return if($min != 0, $min, null);
}
