$ns: "fdt-";
@import 'style/global.scss';

.#{$ns}CartListItem {
    border-radius: 8px;
    will-change: background-color;

    transition: .2s ease-in-out;

    &__cell {
        padding: scale-h(12) 0;
    }

    &__quantity {
        width: scale-w(100);

        text-align: center;
    }

    &__index {
        padding: 0 scale-w(12);

        font-size: scale-h(16);
        font-weight: normal;
        font-style: normal;
        line-height: scale-h(22);

        color: #fff;
    }

    &__cell:first-child {
        padding-left: 14px;

        border-radius: 8px 0 0 8px;
    }

    &__cell:last-child {
        padding-right: 14px;

        border-radius: 0 8px 8px 0;
    }

    &__img {
        width: 100%;
        max-width: 100%;
        height: 100%;
    }

    .#{$ns}Card {
        // overflow: visible;

        width: scale-h(64);
        height: scale-h(64);
        margin-right: scale-w(10);

        border-radius: 8px;

        &__index {
            top: -20px;
            left: -20px;
        }
    }

    &__quantityIcons {
        position: relative;

        display: flex;
        justify-content: center;

        white-space: nowrap;
    }

    &__quantityText {
        display: flex;
        align-items: center;
    }

    &__icon {
        display: none;

        transition: .3s ease-in-out;
        will-change: transform;
        will-change: background-color;

        &:focus {
            transform: scale(1.425);
        }

        &_visible {
            display: inline-block;
        }
    }

    &__quantitySlider {
        display: flex;
        align-items: center;

        color: #fff;
    }

    &__qtyItems {
        display: flex;
        justify-content: center;

        margin: 0 scale-w(14);
        padding: scale-h(8) scale-w(12);

        border-radius: scale-w(16);
        background: rgba(255, 255, 255, .12);

        transition: transform .3s ease-in-out;

        &_focused {
            background-color: $sc-accent-green;

            transform: scale(1.2);
        }
    }

    &__qtyIconLess {
        position: relative;
        top: 0;
    }

    &__qtyIconMore {
        position: relative;
        top: 0;

        transform: rotate(180deg);
    }

    &__qtyItem {
        display: flex;
        align-items: center;

        font-size: scale-h(16);
        font-weight: 600;
        line-height: scale-h(20);

        &::before {
            top: 1px;

            display: block;

            width: scale-h(10);
            height: scale-h(10);
            margin-right: 2px;

            content: '';

            background-image: url('../../../assets/icons/cross-white.svg');
            background-repeat: no-repeat;
            background-size: contain;
        }
    }

    &__qtyIconLess,
    &__qtyIconMore {
        display: inline-block;
        flex-grow: 0;

        width: scale-h(10);
        height: scale-h(18);

        background: url('./icon.svg');
        background-repeat: no-repeat;
        background-size: contain;
    }
}
